export default {
	data() {
		return {
			StatusBar: this.StatusBar,
			CustomBar: this.CustomBar
		};
	},
	name: 'cu-custom',
	computed: {
		style() {
			var StatusBar = this.StatusBar;
			var CustomBar = this.CustomBar;
			var bgImage = this.bgImage;
			var style = `height:${CustomBar}px;padding-top:${StatusBar}px;`;
			if (this.bgImage) {
				style = `${style}background-image:url(${bgImage});`;
			}
			return style;
		}
	},
	props: {
		bgColor: {
			type: String,
			default: ''
		},
		isBack: {
			type: [Boolean, String],
			default: false
		},
		islist: {
			type: [Boolean, String],
			default: false
		},
		bgImage: {
			type: String,
			default: ''
		},
		ischa: {
			type: [Boolean, String],
			default: false
		},
		skin: {
			type: [Boolean, String],
			default: false
		},
		tubei:{
			type:Boolean,
			default: true
		}
	},
	methods: {
		BackPage() {
			if (this.islist) {
				uni.navigateTo({
					url: '/pages/pagesIndex/pagesIndex?id=3'
				});
			} else {
				uni.navigateBack({
					delta: 1
				});
			}
		}
	} 
};
